.error {
  position: absolute;
  height: 100%;
  right: -10px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 5px;
  width: min-content;
}

.error__wrap {
  width: 100%;
  height: 100%;
  position: relative;
}

.error__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid red;
  border-radius: 50%;
}

.error__icon > div {
  width: 100%;
  height: 100%;
  position: relative;
}

.error__icon > div:before {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 3px;
  height: 15px;
  width: 3px;
  background-color: red;
}

.error__icon > div:after {
  content: '';
  position: absolute;
  display: block;
  width: 3px;
  height: 3px;
  background-color: red;
  left: 50%;
  transform: translateX(-50%);
  bottom: 3px;
}


.error__icon:hover + .error__text {
  opacity: 1;
  transition: opacity ease .4s;
}

.error__text {
  font-size: 14px;
  opacity: 0;
  position: absolute;
  text-align: left;
  left: 50px;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #4C9A95;
  border-radius: 25px;
  padding: 15px;
  background-color: white;
  transition: opacity ease .4s;
  min-width: 220px;
}